import { Component, OnInit } from '@angular/core';
// import { Md5 } from 'ts-md5/dist/md5';
import { Router } from '@angular/router';
import { NgForm } from '@angular/forms';
// import { TranslationService } from '../services/translation.service';
import { UserService } from '../services/user.service';
import { LocalStorage, SessionStorage, LocalStorageService } from 'ngx-webstorage';
// import { SessionService } from '../services/sessionService';

@Component({
    templateUrl: 'login.component.html',
    styleUrls: ['login.component.css']
})
export class LoginComponent implements OnInit {

    formError: string;
    submitting = false;
    companyLogoImagePath: string;
    username: string = "";
    password: string = "";
    showError = "";
    lables: any;

    constructor(public router: Router, public userService: UserService, public storage: LocalStorageService) {
        // this.companyLogoImagePath = './assets/img/gs_logo_badge.png';
        // this.companyLogoImagePath = './assets/title-bar/companyLogo1.png'
    }

    ngOnInit() {
    }

    onSubmit(signInForm: NgForm) {
        if (signInForm.valid) {
            this.formError = "";
            this.submitting = true;
            //   var md5 = new Md5();
            //   var password = '' + md5.appendStr(this.password).end();
            this.userService.signIn(this.username, btoa(this.password))
                .subscribe((data) => {
                    this.storage.store("APIkey",data.data.Apikey);
                    this.storage.store("userLogedIn", "Yes")
                    this.storage.store("emailId", this.username)
                    this.router.navigate(['/dashboard']);
                    if (typeof data === "string") {
                        this.showError = data;
                        this.submitting = false;
                    }
                    var code = this.userService.observeLanguageSession();
                    //   this.translationService.translate(code)
                    //     .subscribe(
                    //       data => {
                    //         //this.lables = data;
                    //         this.sessionService.setSession("selectedLanguage", data);
                    //       },
                    //       err => console.log('get error: ', err)
                    //     );
                },
                    (err) => {
                        this.submitting = false;
                        this.formError = 'You are not authorized.';
                    }
                );
        }
    }

}
